/****************************/
/* swiper                   */
/****************************/
import Swiper from 'swiper';

import '../node_modules/swiper/dist/css/swiper.min.css';


const swiperSlide = function(){

    /****************************/
    /* Circle                   */
    /****************************/

    // Drag check
    var isMove = false;
    var tsX;
    var circleWrap = $('.circle_wrap');
    var circle = $('.circle');
    var cItem = $('.circle .c_item');
    var isCircle = false;
    var endDir;
    var movDir;
    var prevPosX;


    // circle
    circle.on('mousedown touchstart', function(){
        isCircle = true;
        circle.addClass('active');
    });
    circle.on('mouseup touchend', function(){
        isCircle = false;
        circle.removeClass('active');
        
        //check last direction
        // console.log(endDir,prevPosX);
    });


    $(document).on('mousedown touchstart', function (e){
        isMove = true;
        if(e.type == 'mousedown'){
            tsX = (Math.floor(e.pageX))/4;
    
        } else if(e.type == 'touchstart'){
            tsX = (Math.floor(e.originalEvent.touches[0].pageX))/2;
        }

        $(document).on('mousemove touchmove', function (e){
            if(isMove){
                if(e.type == 'mousemove'){
                    var posX = (Math.floor(e.pageX))/4;
    
                } else if(e.type == 'touchmove'){
                    var posX = (Math.floor(e.originalEvent.touches[0].pageX))/2;
                }

                /*  
                    ** tobe : 같은 방향으로 드래그시 deg 누적
                    1.드래시 마지막 prevPosX & 방향저장
                    2.같은 방향이면 prevPosX 누적하여 rotate (prevPosX + posX)
                    3.아니면 그대로 posX 
                */
                
                if(isCircle){
              
                    //1.드래그 끝나고 떼는 시점에 save last prevPosX & Direction
                    if(posX - tsX > 0){
                        movDir = 'right';
                        $(document).off('mouseup touchend').on('mouseup touchend', function(e){
                            endDir = 'right'
                            prevPosX = posX;
                        });

                    } else {
                        movDir = 'left';
                        // posX = -(posX); //left direction set minus
                        // 좌측으로 돌릴때 - 처음에만 먹도록 수정해야함..?
                        // 방향전환시 차이가 큼
                        $(document).off('mouseup touchend').on('mouseup touchend', function(e){
                            endDir = 'left'
                            prevPosX = posX;
                        });
                    }

                    // console.log(posX, prevPosX, posX - tsX);
                    if(movDir === endDir){
                        // console.log('같은 방향');
                        // 두번 돌릴때는 괜찮은데 세번째부터 잘 안돌아감
                        circle.css({
                            // 괄호주의!
                            transform: 'rotate('+(prevPosX+10+(posX - tsX))+'deg)'
                        });

                    } else{
                        // console.log('다른 방향');
                        circle.css({
                            transform: 'rotate('+(posX)+'deg)'
                        });
                        cItem.css({
                            transform: 'rotate('+-(posX)+'deg)'
                        });
                    }


                }
            }
        });
    });

    $(document).on('mouseup touchend', function(e){
        isMove = false;
        if(isCircle){
            isCircle = false;
            circle.removeClass('active');
        }
    });



    var circleBoard = $('.circle_wrap .circle');
    var circleItem = $('.circle_wrap .c_item');
    var move = 2.3;

    var mySwiperCircle = new Swiper('.swiper-container.circle_slider', {
        // loop: true,
        slidesPerView: 5,
        freeMode: true,
        speed : 300,
        on: {
            progress: function (progress) {
                // console.log(progress);

                //if loop progress set
                // if(progress < 0.1 || progress ==1){
                //     progress = 0;
                // }
                
                circleBoard.css({
                    transform:'rotate('+ -progress*(720/move) +'deg)'
                });
                circleItem.css({
                    transform:'rotate('+ progress*(720/move) +'deg)'
                });
            },
            slideChange: function(){
                console.log(mySwiperCircle.activeIndex);
            }
        }
    }); 



    /****************************/
    /* mySwiperTab              */
    /****************************/

    // not use spaceBetween  
    var gutter = 20;
    var slideAmount = 10; // slideAmount

    var mySwiperTab = new Swiper('.swiper-container.tabslide', {
        navigation: {
            nextEl: '.tabslide .swiper-button-next',
        },        
        slidesPerView: 'auto',
        freeMode: true
    }); 



    var mySwiperTabGroup = new Swiper('.tabslide_group .swiper-container.tabslide', {
        navigation: {
            nextEl: '.tabslide_group .tabslide .swiper-button-next',
        },        
        slidesPerView: 'auto',
        freeMode: true
    }); 


    var mySwiperCont = new Swiper('.swiper-container.tabcont', {
        on: {
            slideChange: function(){
                $('.tabslide_group .tabslide a').removeClass('on');
                $('.tabslide_group .tabslide .swiper-slide').eq(mySwiperCont.activeIndex).find('a').addClass('on');

                // tabNavSwiper Auto Move
                if (mySwiperCont.previousIndex < mySwiperCont.activeIndex) {
                    mySwiperTabGroup.slideTo(mySwiperCont.activeIndex - 2);
                } else {
                    mySwiperTabGroup.slideTo(mySwiperCont.activeIndex - 1);
                }
                
                var line = $('.tabslide_group').find('.active_line');
                var activeTab = $('.tabslide_group .tabslide').find('a.on').parent('.swiper-slide');

                var tabWid = Math.floor(activeTab.innerWidth());
                var tabXpos = Math.floor(activeTab.position().left);

                // line move
                line.css({
                    'width': tabWid,
                    'left': gutter + tabXpos,
                    'transition':'.3s'
                });

                // first tab prevent progress transform
                if (mySwiperCont.activeIndex == 0){
                    line.css({
                        'transform':'scale(1) translateX(0)'
                    });
                }
            },
            progress: function (progress) {
                var slideFullProgress = 1 / (slideAmount - 1); // slideFullProgress
                var slideProgress = progress % slideFullProgress / slideFullProgress; // slideProgress[0,1]
            
                var line = $('.tabslide_group').find('.active_line');
                
                var scaleX = 1+slideProgress;
                var scaleXLeft = 1+(slideProgress-0.5);

                if (progress > 0 && progress <= 1) {
                  if (slideProgress <= 0.5) {
                    line.css({
                        'transform':'scaleX('+scaleX+')',
                    });
                  } else {
                    line.css({
                        'transform':'scaleX('+scaleXLeft+') translateX('+ -20 * scaleXLeft +'px)',
                    });
                  }
                }
            },
        }
    }); 


    // tabslide
    $('.tabslide').each(function(){
        var _this = $(this);
        var tabs = _this.find('a');
        var line = _this.find('.active_line');
        var tabWid = '';
        var tabXpos = '';
        var activeTab = _this.find('a.on').parent('.swiper-slide');

        // init line
        line.css({
            width:_this.find('.swiper-slide').eq(0).innerWidth(),
            left:gutter
        });

        tabs.on('click', function(){

            activeTab = $(this).parent('.swiper-slide');

            tabs.removeClass('on');
            $(this).addClass('on');

            tabWid = Math.floor(activeTab.innerWidth());
            tabXpos = Math.floor(activeTab.position().left);

            // tab move
            var tabIdx =  activeTab.index();
            // mySwiperTab.slideTo(tabIdx);
            // 각각 동작하도록 분리시켜야함

            // line move
            line.css({
                'width': tabWid,
                'left': gutter + tabXpos,
                'transition':'.3s'
            });
        });

        // flex mode center reposition
        $(window).on('resize', function(){
            if($('.tabslide').hasClass('flex')){

                if(_this.parents().hasClass('tabslide_group')){
                    activeTab = $('.tabslide_group .tabslide').find('a.on').parent('.swiper-slide');
                }

                tabWid = Math.floor(activeTab.innerWidth());
                tabXpos = Math.floor(activeTab.position().left);
                line.css({
                    'width': tabWid,
                    'left': gutter + tabXpos,
                    'transition':'0s'
                });
            }
        });


    });

    // tabslide group
    $('.tabslide_group').each(function(){
        var _this = $(this);
        var tabs = _this.find('.tabslide a');

        tabs.on('click', function(){
            // tab move
            var activeTab = $(this).parent('.swiper-slide');
            var tabIdx =  activeTab.index();
            mySwiperCont.slideTo(tabIdx);
        });
    });
   
}


export default swiperSlide;